/* ===============================
       gray color Variables
 =============================== */
$gray-100: #fafafa !default;
$gray-200: #eee !default;
$gray-300: #f1f5f9 !default;
$gray-400: #ddd !default;
$gray-500: #cfcfcf !default;
$gray-600: #646a7c !default;
$black: #000;
$white: #fff;

/* ===============================
       map gray color
 =============================== */
$grays: (
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
);

/* ===============================
        Color Variables
 =============================== */
$primary: #5260ab;
$light-purple: #929ed9;
$dark-purple: #3d3248;
$blue: #303a6c;
$secondary: #51bfad;
$orange: #f69c5d;
$warning: #ffd36f;
$danger: #fc4b3c;
$success: #36b600;

/* ===============================
        map theme color
 =============================== */
$theme-colors: (
  primary: $primary,
  secondary: $secondary,
  warning: $warning,
  danger: $danger,
  success: $success,
  orange: $orange,
  gray: $gray-600,
  white: $white,
  black: $black,
  light: $gray-100,
  gray-200: $gray-200,
  gray-300: $gray-300,
  gray-400: $gray-400,
  gray-500: $gray-400,
  blue: $blue,
  light-purple: $light-purple,
  dark-purple: $dark-purple,
);

/* ===============================
        Body color Variables
 =============================== */
$body-color: $gray-600;

/* ===============================
         Fonts Variables
 =============================== */
$font-path: "../../fonts";

$font-size-base: 1rem; // 16px
$base-font-family: "Cairo", sans-serif;

$h1-font-size: $font-size-base * 2.5;
$h2-font-size: $font-size-base * 2.25; // 36px;
$h3-font-size: $font-size-base * 1.875; // 30px
$h4-font-size: $font-size-base * 1.5; // 24px
$h5-font-size: $font-size-base * 1.125; // 18 px
$h6-font-size: $font-size-base; // 16px

$font-size-lg: $font-size-base;

$small-font-size: 0.813rem; // 13px
$xsmall-font-size: 0.875rem; // 14px
$large-font-size: 3rem; // 48px

$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size,
  7: $small-font-size,
  8: $xsmall-font-size,
  9: $font-size-lg,
  10: $large-font-size,
);

/* ===============================
       Style anchor elements
 =============================== */
$link-color: $blue !default;
$link-decoration: none !default;
$link-hover-decoration: underline !default;
$link-hover-color: $secondary;

/* ===============================
      Border
 =============================== */
$border-width: 1px;
$border-radius: 0.313rem;
$border-radius-lg: 0.625rem;

$border-base: $border-width solid $gray-600;

/* ===============================
      Icons
 =============================== */
$icon-size-base: 1rem;

$icon-sizes: (
  1: $icon-size-base,
  2: $icon-size-base * 1.25,
  3: $icon-size-base * 1.5,
  4: $icon-size-base * 1.75,
  5: $icon-size-base * 2,
  6: $icon-size-base * 2.5,
);

/* ===============================
// Container padding
 =============================== */

$grid-gutter-width: 1.5rem !default;
$container-padding-x: $grid-gutter-width * 3 !default;

$input-btn-font-size: $h5-font-size !default;

$text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

/* ===============================
// Box shadow
 =============================== */
$box-shadow-lg: 0px 30px 100px rgba(8, 15, 21, 7%);
$box-shadow-sm: 0px 30px 100px rgba(8, 15, 21, 0.0749392);

/* ===============================
// Form
 =============================== */
$input-border-color: rgba($primary, 0.14);

/* ===============================
// Spacer
 =============================== */
$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: $spacer * 2.75,
  15: $spacer * 15,
);

$list-group-action-active-bg: lighten($primary, 48%);
$list-group-hover-bg: lighten($primary, 48%);

$position-values: (
  0: 0,
  15: 15%,
  50: 50%,
  100: 100%,
);

$btn-padding-y: 0.5rem;
$btn-padding-x: 3rem;
