@import "../../assets/stylesheet/helpers/custom-variables";

.carousel {
  .title {
    margin-top: 72px;
    margin-bottom: 3.563rem !important;
    font-size: 3rem;
    font-weight: 200 !important;
    text-align: center;
  }

  .icon {
    right: 15%;
    left: 15%;
    top: -4rem;
  }
  .react-multiple-carousel {
    &__arrow {
      background-color: $secondary;
      &--right {
        right: 0;
        &::before {
          content: url("data:image/svg+xml,%3Csvg width='29' height='18' viewBox='0 0 29 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.0142 1.98828L27.5477 8.61252L20.0142 16.4999' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M27.5475 8.82227H1.28271' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E") !important;
        }
      }

      &--left {
        left: 0;
        &::before {
          content: url("data:image/svg+xml,%3Csvg width='29' height='18' viewBox='0 0 29 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.98535 1.98828L1.45185 8.61252L8.98535 16.4999' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M1.45199 8.82227H27.7168' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") !important;
        }
      }
    }
  }

  .rtl.react-multiple-carousel {
    &__arrow {
      &--left {
        right: 0;
        left: auto;
        &::before {
          content: url("data:image/svg+xml,%3Csvg width='29' height='18' viewBox='0 0 29 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.0142 1.98828L27.5477 8.61252L20.0142 16.4999' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M27.5475 8.82227H1.28271' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E") !important;
        }
      }

      &--right {
        right: auto;
        left: 0;
        &::before {
          content: url("data:image/svg+xml,%3Csvg width='29' height='18' viewBox='0 0 29 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.98535 1.98828L1.45185 8.61252L8.98535 16.4999' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M1.45199 8.82227H27.7168' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") !important;
        }
      }
    }
  }

  .rtl.react-multiple-carousel__arrow--right::before {
    content: "\e824";
  }
}
