.footer {
  border-top: 1px solid #ced4da;
}
 .links ul {
  padding: 0 !important;
}
 .links li {
  list-style: none;
}

 .links li a,
 .logo p {
  color: #5260ab;
}
 .icons a {
  color: #a2a9d0;
}

 .footer input {
  padding: 4px 0 !important;
  margin-left: -6px;
}
 .btn {
  background-color: #5260ab !important;
  border-color: #5260ab !important;
  padding: 5px 45px !important;
}
.copy {
  color: #5260ab;
}
@media (max-width: 768px) {
 .row {
    padding-left: 50px;
    padding-right: 50px;
  }
 .footer input {
    width: 100%;
    margin-bottom: 20px;
  }
 .btn {
    margin: auto;
  }
}
