.video-card {
  &__title {
    height: 27rem;
    position: relative;
    padding: 6rem 4.5rem;
    @media screen and (max-width: 767px) {
      padding: 6rem 0.9rem;
    }

    &__icon {
      right: -6.5rem;
      left: -6.5rem;
      top: 0;
      height: 29.875rem;
      width: 44.625rem;

      @media screen and (max-width: 767px) {
        height: 13.875rem;
        width: 23.625rem;
      }
    }
  }
  &__items {
    margin-top: -15rem;
  }
}
